export default {
  name: 'MeTeam',
  data (){
    return {
      list: [],
      count: 0,//总人数
    }
  },
  methods: {
    getTeamData(){
      this.http.get(this.ports.me.team, res =>{
        this.$store.commit('changeLoading',false)
        if(res.success){
          let data = res.data
          this.count = data.count
          this.list = data.res
        }else{
          this.showToastTxtOnly(res.msg)
        }
      })
    }
  },
  mounted (){
    this.getTeamData()
  }
}



